<template>
  <div>
    <div style="background-color:#303035;height: 50px;">
      <span  @click="toIndex" style="float: left;font-family: 华文行楷;color: #b5bccb;font-size: 30px;
      margin-left: 10px;margin-top: 10px">首页</span>
      <span style="font-family: 华文行楷;color: #42b983;font-size: 40px">E租房管理员中心</span>
    </div>
    <el-container style="height: 700px; border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="default_openeds_array" @select="handleSelect" @open="handleOpen">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-goods"></i>信息管理</template>
            <el-menu-item-group>
<!--              <el-submenu index="2-1">-->
<!--                <template slot="title">用户信息管理</template>-->
<!--                <el-menu-item index="2-1-1">正在出租</el-menu-item>-->
<!--                <el-menu-item index="2-1-2">被预定</el-menu-item>-->
<!--                <el-menu-item index="2-1-3">已出租</el-menu-item>-->
<!--              </el-submenu>-->
<!--              <el-submenu index="2-2">-->
<!--                <template slot="title">房屋信息管理</template>-->
<!--                <el-menu-item index="2-2-1">我的预定</el-menu-item>-->
<!--                <el-menu-item index="2-2-2">已租到</el-menu-item>-->
<!--              </el-submenu>-->
                  <el-menu-item index="1-1">用户信息管理</el-menu-item>
                  <el-menu-item index="1-2">房屋信息管理</el-menu-item>

            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container>
        <el-header style="text-align: right">
          <el-dropdown @command="handleCommand">
            <i class="el-icon-s-tools" style="font-size: 25px"></i>
            <el-dropdown-menu slot="dropdown" style="font-size:90px">
              <el-dropdown-item command="a">aaa</el-dropdown-item>
              <el-dropdown-item command="b">登出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <span style="font-family: 华文行楷;color: #3c35b9;font-size: 20px">欢迎你</span>
          <span style="font-family: 华文行楷;color: #b9261e;font-size: 40px">狗管理</span>
        </el-header>

        <el-main>
          <el-table
            v-if="this.tableSelect=='1-1'"
            :data="tableData"
            stripe
            border
            element-loading-text="正在加载..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            style="width: 100%">
            <el-table-column prop="pname" label="用户昵称" width="180">
            </el-table-column>
            <el-table-column prop="username" label="用户名（手机号）" width="140">
            </el-table-column>
            <el-table-column  label="" width="140">
            </el-table-column>
            <el-table-column  label=" " width="140">
            </el-table-column>
            <el-table-column  label=" " width="140">
            </el-table-column>
            <el-table-column  label=" " width="140">
            </el-table-column>
            <el-table-column  label=" " width="140">
            </el-table-column>
            <el-table-column  label=" " width="140">
            </el-table-column>
            <el-table-column
              fixed="right"
              width="200"
              label="行为">
              <template slot-scope="scope">
                <el-button style="padding: 3px" @click="del(scope.row)" size="mini" type="danger">锁定
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-table
            v-if="this.tableSelect=='1-2'"
            :data="tableData"
            stripe
            border
            element-loading-text="正在加载..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            style="width: 100%">
            <el-table-column prop="name" label="标题" width="180">
            </el-table-column>
            <el-table-column prop="addressCity" label="城市" width="140">
            </el-table-column>
            <el-table-column prop="addressQx" label="区/县" width="140">
            </el-table-column>
            <el-table-column prop="addressJd" label="街道" width="140">
            </el-table-column>
            <el-table-column prop="money" label="租金" width="120">
            </el-table-column>
            <el-table-column prop="area" label="面积" width="120">
            </el-table-column>
            <el-table-column prop="czfs" label="出租方式" width="120">
            </el-table-column>
            <el-table-column prop="cx" label="朝向" width="120">
            </el-table-column>
            <el-table-column
              fixed="right"
              width="200"
              label="操作">
              <template slot-scope="scope">
                <el-button style="padding: 3px" @click="del(scope.row)" size="mini" type="danger">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
    export default {
        name: "AdminCenter",
      data(){
        return {
          tableSelect:'',
          default_openeds_array:['1'],
          tableData: [],
          house:{
            pid:0,
            state:''
          }
        }
      },
      mounted() {
      },
      methods:{
        handleOpen(key){
          this.default_openeds_array.push(key);
        },
        handleCommand(command){
          switch (command) {
            case 'a' :
              console.log(command)
              break;
            case 'b' :
              this.$router.push('/')
              break;
          }
        },
        toIndex(){
          this.$router.push({path:'/'})
        },
        getHouseByState(){
          this.postRequest("house/find",this.house).then(resp => {
            if (resp.data.description=='SUCCESS'){
              this.tableData = resp.data.detail
            }
          })
        },
        getHouseByState1(){
          this.postRequest("house/find1",this.house).then(resp => {
            if (resp.data.description=='SUCCESS'){
              this.tableData = resp.data.detail
            }
          })
        },
        handleSelect(key, keyPath) {
          switch (key) {
            case '1-1':this.tableSelect = '1-1'
              this.getUserInfo()
              break;
            case '1-2':this.tableSelect='1-2'
              this.getHouseInfo()
              break;
          }
        },
        getUserInfo(){
          this.postRequest("/user/findAllUsers").then(resp => {
            this.tableData = resp.data.detail
          })
        },
        getHouseInfo(){
          this.postRequest("/house/findAllHouses").then(resp => {
            this.tableData = resp.data.detail
          })
        },
        del(data){
          console.log(data.hid)
          this.$confirm('此操作将永久删除【' + data.name + '】, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteRequest("/house/"+data.hid).then(resp => {
              if (resp.data.description=='SUCCESS') {
                this.getHouseByState();
                this.$message({
                  type: 'success',
                  message: '删除成功'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },


      }
    }
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
</style>



