<template>
  <div>
<!--    <div class="background">-->
<!--      <img :src="imgSrc" width="100%" height="100%" alt="" />-->
<!--    </div>-->
    <div style="background-color: #303035;height: 50px">
    <el-row>
      <el-col :span="4">
        <div class="title" @click="toIndex">首页</div>
      </el-col>
      <el-col :span="4" style="float: right">
        <i class="el-icon-user-solid" style="font-size: 25px;color:#9399a5"></i>
        <el-link v-if="this.loginStatus==0" @click="logine()" class="title1" :underline="false">登录/注册</el-link>
        <el-dropdown v-else @command="handleCommand" style="margin-top: 15px">
              <span class="el-dropdown-link">
                {{user.pname}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">我的消息</el-dropdown-item>
            <el-dropdown-item command="b">看房记录</el-dropdown-item>
            <el-dropdown-item command="c" divided>退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
    </div>
        <el-row>
          <el-col :span="10" :offset="6">
              <h3>发布出租房源</h3>
          </el-col>
        </el-row>
    <el-row>
      <el-col :span="10" :offset="6">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="name">
        <el-input v-model="ruleForm.name" placeholder="起个厉害点的标题吧~~~"></el-input>
      </el-form-item>
      <el-form-item label="所在地" prop="city">
        <el-cascader
          style="float:left"
          v-model="ruleForm.city"
          :options="options"
          ></el-cascader>
      </el-form-item>
      <el-form-item label="小区名称" prop="xqname">
        <el-input v-model="ruleForm.xqname" style="width:150px;float: left"></el-input>
      </el-form-item>
      <el-form-item label="面积" prop="area">
        <el-input size="small" style="width:150px;float: left" v-model="ruleForm.area"></el-input>
        <span style="float: left;margin-left: 10px;color:red">平方米</span>
      </el-form-item>
      <el-form-item label="租金" prop="money">
        <el-input size="small" style="width:150px;float: left" v-model="ruleForm.money"></el-input>
        <span style="float: left;margin-left: 10px;color:red">元/月</span>
      </el-form-item>
      <el-form-item label="房屋户型" prop="housemodel">
      <el-select v-model="ruleForm.housemodel" style="float:left" placeholder="请选择...">
        <el-option label="一室" value="一室"></el-option>
        <el-option label="两室" value="两室"></el-option>
        <el-option label="三室" value="三室"></el-option>
        <el-option label="三室以上" value="三室以上"></el-option>
      </el-select>
    </el-form-item>
      <el-form-item label="朝向" prop="cx">
        <el-select v-model="ruleForm.cx" style="float:left" placeholder="请选择...">
          <el-option label="东西" value="东西"></el-option>
          <el-option label="南北" value="南北"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="出租方式" prop="czfs">
        <el-radio-group v-model="ruleForm.czfs" style="margin-left: -310px">
          <el-radio label="整租"></el-radio>
          <el-radio label="合租"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="有无电梯" prop="dt">
        <el-radio-group v-model="ruleForm.dt" style="margin-left: -330px">
          <el-radio label="1">有</el-radio>
          <el-radio label="0">无</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="特殊" prop="tag">
        <el-checkbox-group v-model="ruleForm.tag">
          <el-checkbox label="防疫服务"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="房屋配套" prop="fwpt">
        <el-checkbox-group v-model="ruleForm.fwpt">
          <el-checkbox label="床"></el-checkbox>
          <el-checkbox label="洗衣机"></el-checkbox>
          <el-checkbox label="空调"></el-checkbox>
          <el-checkbox label="冰箱" ></el-checkbox>
          <el-checkbox label="卫生间"></el-checkbox>
          <el-checkbox label="可做饭"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="房屋图片" prop="pic">
      <el-upload
        ref="upload"
        action="/upload"
        name="picture"
        list-type="picture-card"
        :limit="3"
        :on-exceed="onExceed"
        :before-upload="beforeUpload"
        :on-preview="handlePreview"
        :on-success="handleSuccess"
        :on-remove="handleRemove">
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>
    </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">立即出租</el-button>
        <el-button @click="test()">取消</el-button>
      </el-form-item>
    </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "rentOut",
      data(){
        var validateMobilePhone = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('不可为空'));
          } else {
            if (value !== '') {
              var reg=/(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/
              if(!reg.test(value)){
                callback(new Error('请输入有效数字'));
              }
            }
            callback();
          }
        };
          return{
            options: [{
              value: '淄博',
              label: '淄博',
              children: [{
                value: '张店区',
                label: '张店区',
                children: [{
                  value: '柳泉路',
                  label: '柳泉路'
                }, {
                  value: '理工路',
                  label: '理工路'
                }]
              }, {
                value: '高青县',
                label: '高青县',
                children: [{
                  value: '民主街',
                  label: '民主街'
                }, {
                  value: '芦湖路',
                  label: '芦湖路'
                }]
              }]
            },{
              value: '青岛',
              label: '青岛',
              children: [{
                value: '黄岛区',
                label: '黄岛区',
                children: [{
                  value: '科大路',
                  label: '科大路'
                }, {
                  value: '政府路',
                  label: '政府路'
                }]
              }, {
                value: '市南区',
                label: '市南区',
                children: [{
                  value: '栈桥路',
                  label: '栈桥路'
                }, {
                  value: '火车站路',
                  label: '火车站路'
                }]
              }]
            }],
            // imgSrc:require('../assets/wallhaven-73qeqy.png'),
            ruleForm: {
              name: '',
              city:[],
              money:'',
              area:'',
              xqname:'',
              housemodel:'',
              czfs:'',
              cx:'',
              tag: [],
              fwpt:[],
              dt: '',
              picture:[],
              pid:''
            },
            loginStatus : 0,
            input :"",
            user : "",
            //文件上传的参数
            dialogImageUrl: '',
            dialogVisible: false,
            //图片列表（用于在上传组件中回显图片）
            fileList: [{name: '', url: ''}],
            rules: {
              name: [
                { required: true, message: '请输入标题', trigger: 'blur' },
                { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
              ],
              xqname: [
                { required: true, message: '请输入小区', trigger: 'blur' },
                { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }
              ],
              money: [{ required: true,validator: validateMobilePhone, trigger: "blur" }],
              area: [{ required: true,validator: validateMobilePhone, trigger: "blur" }],

              city: [
                { required: true, message: '请选择所在城市', trigger: 'change' }
              ],
              cx: [
                { required: true, message: '请选择房屋朝向', trigger: 'change' }
              ],
              housemodel: [
                { required: true, message: '请选择房屋户型', trigger: 'change' }
              ],
              tag: [
                { type: 'array',  message: '请至少选择一个', trigger: 'change' }
              ],
              fwpt: [
                { type: 'array',  message: '请至少选择一个', trigger: 'change' }
              ],
              czfs: [
                { required: true, message: '请选择出租方式', trigger: 'change' }
              ],
              dt: [
                { required: true, message: '请选择有无电梯', trigger: 'change' }
              ]
            }
          };
      },
      mounted() {
        let token = localStorage.getItem("token");
        this.getUserInfo(token);
      },
      methods:{
        toIndex(){
          this.$router.push({path:'/'})
        },
        getUserInfo(token){
          if (token != null){
            this.postRequest('/user/dologin',token).then(resp=>{
              if (resp!=null){
                this.user = resp.data;
                this.loginStatus = 1;
                this.ruleForm.pid = resp.data.id
              }
            })
          }
        },
        handleCommand(command) {
          switch (command) {
            case 'a' :alert('click on item ' + command)
              break;
            case 'b' :alert('b')
              break;
            case 'c' : this.logout()
              break;
          }

        },
        logout(){
          localStorage.removeItem("token");
          // sessionStorage.setItem("loginstatus",0)
          this.getRequest('user/logout')
          this.loginStatus=0
          this.$router.push('/')
        },
        test(){
          console.log(this.ruleForm)
          this.postRequest("/house/add",this.ruleForm).then(resp =>{
            console.log(resp)
          })
        },
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              console.log(this.ruleForm)
              this.postRequest("/house/add",this.ruleForm).then(resp =>{
                console.log(resp)
                if (resp.data.code==200){
                  this.$router.push("/")
                }
              })
            } else {
              this.$message({
                type: 'info',
                message: '失败',
              });
              return false;
            }
          });
        },
        resetForm() {
          this.$router.go(-1)
        },
        //文件上传成功的钩子函数
        handleSuccess(res, file) {
          this.$message({
            type: 'info',
            message: '图片上传成功',
            duration: 6000
          });
          if (file.response.success) {
            this.ruleForm.picture += file.response.message+','; //将返回的文件储存路径赋值picture字段
          }
        },
        //删除文件之前的钩子函数
        handleRemove(file, fileList) {
          console.log(file, fileList);
          this.$message({
            type: 'info',
            message: '已删除原有图片',
            duration: 6000
          });
        },
        //点击列表中已上传的文件事的钩子函数
        handlePreview(file) {
        },
        //上传的文件个数超出设定时触发的函数
        onExceed(files, fileList) {
          this.$message({
            type: 'info',
            message: '最多只能上传3个图片',
            duration: 6000
          });
        },
        //文件上传前的前的钩子函数
        //参数是上传的文件，若返回false，或返回Primary且被reject，则停止上传
        beforeUpload(file) {
          const isJPG = file.type === 'image/jpeg';
          const isGIF = file.type === 'image/gif';
          const isPNG = file.type === 'image/png';
          const isBMP = file.type === 'image/bmp';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isJPG && !isGIF && !isPNG && !isBMP) {
            this.$message.error('上传图片必须是JPG/GIF/PNG/BMP 格式!');
          }
          if (!isLt2M) {
            this.$message.error('上传图片大小不能超过 2MB!');
          }
          return (isJPG || isBMP || isGIF || isPNG) && isLt2M;
        },
      }
    }
</script>

<style scoped>
  .background{
    margin-top: 32px;
    width:100%;
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: absolute;
  }
  .el-dropdown-link {
    cursor: pointer;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title {
    margin-top: 10px;
    font-size: 30px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title1 {
    margin-top: 10px;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }
</style>



