import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import index from './views/Index'
import login from './views/Login'
import register from './views/register'
import house from './views/HouseInfo'
import rentOut from './views/rentOut'
import PersonalCenter from './views/PersonalCenter'
import adminLogin from "./views/adminLogin";
import AdminCenter from "./views/AdminCenter";
import error from "./views/error";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/houseInfo',
      name: 'houseInfo',
      component: house
    },
    {
      path: '/rentOut',
      name: 'rentOut',
      component: rentOut
    },
    {
      path: '/PersonalCenter',
      name: 'PersonalCenter',
      component: PersonalCenter
    },
    {
      path: '/adminLogin',
      name: 'adminLogin',
      component: adminLogin
    },
    {
      path: '/AdminCenter',
      name: 'AdminCenter',
      component: AdminCenter
    },
    {
      path: '/goBack',
      name: 'goBack',
      redirect:'/'
    },
    {
      path: '/error',
      name: 'error',
      components: error
    },
  ]
})

// 导航守卫
// 使用 router.beforeEach 注册一个全局前置守卫，判断用户是否登陆
// const router = new Router;
// router.beforeEach((to, from, next) => {
//   if (to.path === '/#/login') {
//     // localStorage.removeItem("token");
//     console.log("token11111111111111111111111")
//   } else {
//     let token = localStorage.getItem('token');
//     console.log(token)
//     if (token === 'null' || token === '') {
//       next('/login');
//     } else {
//       next();
//     }
//   }
// });
const router = new Router;
router.beforeEach((to, from, next) => {
  var userInfo = window.localStorage.getItem('token');//获取浏览器缓存的用户信息
  if(userInfo){ //如果有就直接到首页咯
    next();
  } else {
    if(to.path=='/login'){ //如果是登录页面路径，就直接next()
      next();
    } else { //不然就跳转到登录；
      next('/login');
    }

  }
})



