<template>
  <div class="login-container">
    <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>
    <el-form :model="ruleForm2" :rules="rules2"
             status-icon
             ref="ruleForm2"
             label-position="left"
             label-width="0px"
             class="demo-ruleForm login-page">
      <h3 class="title">注册</h3>
      <el-link @click="toLogin()" type="primary" :underline="false" style="margin-left: 280px">返回登录~</el-link>
      <el-form-item prop="pname">
        <el-input type="text"
                  v-model="ruleForm2.pname"
                  auto-complete="off"
                  placeholder="请输入昵称..."
        ></el-input>
      </el-form-item>
      <el-form-item prop="username">
        <el-input type="text"
                  v-model="ruleForm2.username"
                  auto-complete="off"
                  placeholder="请输入手机号..."
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password"
                  v-model="ruleForm2.password"
                  auto-complete="off"
                  placeholder="请输入密码..."
        ></el-input>
      </el-form-item>
      <el-form-item prop="repassword">
        <el-input type="password"
                  v-model="ruleForm2.repassword"
                  auto-complete="off"
                  placeholder="请确认密码..."
        ></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      //确认密码
      var validatePass2 = (rule, value, callback) => {
        if (value !== this.ruleForm2.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      };
      //手机号验证
      var validateMobilePhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('负责人手机号不可为空'));
        } else {
          if (value !== '') {
            this.postRequest("/user/checkusername",this.ruleForm2).then(resp=>{
              console.log(resp)
              if (resp == 1){
                callback(new Error('该手机号已被占用，请重新输入'));
              }else{
                var reg=/^1[3456789]\d{9}$/;
                if(!reg.test(value)){
                  callback(new Error('请输入有效的手机号码'));
                }else {
                  callback();
                }
              }
            })
          }
        }
      };
      //密码验证
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不可为空'));
        } else {
          if (value !== '') {
            var reg=/^1[3456789]\d{9}$/;
            if(!reg.test(value)){
              callback(new Error('请输入有效的密码'));
            }
          }
          callback();
        }
      };
      //昵称验证
      var validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('昵称不可为空'));
        } else {
          if (value !== '') {
            this.postRequest("/user/checkpname",this.ruleForm2).then(resp =>{
              if (resp.data == 1){
                alert('已占用')
                callback(new Error('昵称已被占用'));
              }else{
                var reg=/^[\u0391-\uFFE5A-Za-z]+$/;
                if(!reg.test(value)){
                  callback(new Error('请输入2-4位汉字或英文'));
                }else{
                  callback();
                }
              }
            })
          }
          // callback();
        }
      };
      return {
        imgSrc:require('../../static/img/登录页背景.png'),
        logining: false,
        ruleForm2: {
          pname: '',
          username: '',
          password: '',
          repassword :''
        },
        rules2: {
          pname: [{required: true, message: '昵称不能为空', trigger: 'blur'},
            { min: 2, max: 4, message: "长度在 2 到 4 个字符", trigger: "blur" },
            { validator: validateName, trigger: "blur" }],
          username: [{ validator: validateMobilePhone, trigger: "blur" }],
          password: [{required: true, message: '密码不能为空', trigger: 'blur'},
            { min: 3, max: 6, message: "长度在 3 到 6 个字符", trigger: "blur" }],
          repassword: [{required: true, message: '确认密码不能为空', trigger: 'blur'},
            { validator: validatePass2, trigger: "blur" }]
        },
        checked: false
      }
    },
    methods: {
      toLogin(){
        this.$router.push({path:'login'})
      },
      handleSubmit(event){
        this.$refs.ruleForm2.validate((valid) => {
          if(valid){
            this.logining = true;
            if(this.ruleForm2.password == this.ruleForm2.repassword){
              this.postRequest('/user/register',this.ruleForm2).then(resp => {
                if (resp.data == "success"){
                  this.$confirm('注册成功, 是否跳转登陆页面?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {
                    this.$router.push({path:'login'})
                  }).catch(() => {
                    this.$message({
                      type: 'info',
                      message: '已取消'
                    });
                  });
                }
              })
            }else{
              this.logining = false;
              this.$alert('服务器开小差了', 'info', {
                confirmButtonText: 'ok'
              })
            }
          }else{
            this.$alert('服务器开小差了', 'info')
            return false;
          }
        })
      }
    }
  };
</script>

<style scoped>
  .login-container {
    width: 100%;
    height: 100%;
    z-index:1;
    position: absolute;
  }
  .login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    margin: 180px auto;
    width: 400px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .background{
    width:100%;
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: absolute;
  }
  label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;
  }
</style>



