            pid:0,
            state:''
          }
        }
      },
      mounted() {
        let token = localStorage.getItem("token");
        this.getUserInfo(token);
        this.handleSelect(this.$route.params.tableSelect)
      },
      methods:{
        // 计算时间差
        handleDate(dateTimeStamp) {
          var minute = 1000 * 60;
          var hour = minute * 60;
          var day = hour * 24;
          var result = '';
          var now = new Date().getTime();
          var diffValue = now - Date.parse(dateTimeStamp) ;

          // console.log(diffValue)
          // console.log(now.toString().substring(0,10))
          // console.log(Date.parse(dateTimeStamp).toString().substring(0,10))
          if (diffValue < 0) {
            console.log("时间不对劲,服务器创建时间与当前时间不同步");
            return result = "刚刚";
          }
          var dayC = diffValue / day;
          console.log(dayC)

          var hourC = diffValue / hour;
          console.log(hourC)

          var minC = diffValue / minute;
          if (parseInt(dayC) > 30) {
            result = "" + this.$format(dateTimeStamp,"yyyy-MM-dd");
          }else if(parseInt(dayC) > 1) {
            result = "" + parseInt(dayC) + "天前";
          }else if (parseInt(dayC) == 1) {
            result = "昨天";
          } else if (hourC >= 1) {
            result = "" + parseInt(hourC) + "小时前";
          } else if (minC >= 5) {
            result = "" + parseInt(minC) + "分钟前";
          } else
            result = "刚刚";
          return result;
        },
        handleOpen(key){
          this.default_openeds_array.push(key);
        },
        getMessageInfo(id){
          if (id == null){
            id = this.$route.params.id
          }
          // const id = this.user.id;
          this.getRequest("/user/message/"+id).then(resp => {
            console.log(resp.data.detail)
            this.messageData = resp.data.detail
          })
        },
        agree(data){
          const update = {
            hid:data.hid,
            state:2
          }
          this.putRequest("/house/updateState",update).then(resp => {
            if (resp.data.description=='SUCCESS') {
              this.getHouseByState();
              this.$message({
                type: 'success',
                message: '操作成功'
              });
            }
          })
        },
        notAgree(data){
          const update = {
            hid:data.hid,
            state:0
          }
          this.putRequest("/house/updateState",update).then(resp => {
            if (resp.data.description=='SUCCESS') {
              this.getHouseByState();
              this.$message({
                type: 'success',
                message: '操作成功'
              });
            }
          })
        },
        handleSuccess(res, file) {
          this.$message({
            type: 'info',
            message: '图片上传成功',
            duration: 6000
          });
          if (file.response.success) {
            this.updateInfo.picture += file.response.message+','; //将返回的文件储存路径赋值picture字段
          }
        },
        //删除文件之前的钩子函数
        handleRemove(file, fileList) {
          // console.log(file, fileList);
          // console.log(file.url+',');
          this.$message({
            type: 'info',
            message: '已删除原有图片',
            duration: 6000
          });
          this.updateInfo.picture = this.updateInfo.picture.replace(file.url+',','');
        },
        handleCommand(command){
          switch (command) {
            case 'a' :
              this.$router.push("/rentOut");
              break;
            case 'b' :
              localStorage.removeItem("token");
              this.getRequest('user/logout')
              this.$router.push('/')
              break;
          }
        },
        toIndex(){
          this.$router.push({path:'/'})
        },
        getHouseByState(){
          this.postRequest("house/find",this.house).then(resp => {
            if (resp.data.description=='SUCCESS'){
              this.tableData = resp.data.detail
            }
          })
        },
        getHouseByState1(){
          this.postRequest("house/find1",this.house).then(resp => {
            console.log(resp)
            if (resp.data.description=='SUCCESS'){
              this.tableData = resp.data.detail
            }
          })
        },
        handleSelect(key) {
          switch (key) {
            case '2-1-1':this.tableSelect = '2-1-1'
              this.house.pid = this.user.id
              this.house.state=0
              this.getHouseByState()
              break;
            case '2-1-2':this.tableSelect='2-1-2'
              this.house.pid = this.user.id
                this.house.state=1
              this.getHouseByState()
              break;
            case '2-1-3':this.tableSelect='2-1-3'
              this.house.pid = this.user.id
              this.house.state=2
              this.getHouseByState()
              break;
            case '2-2-1':this.tableSelect='2-2-1'
              this.house.pid = this.user.id
              this.house.state=1
              this.getHouseByState1()
              break;
            case '2-2-2':this.tableSelect='2-2-2'
              this.house.pid = this.user.id
              this.house.state=2
              this.getHouseByState1()
              break;
            case '3':this.tableSelect='3'
              const id = this.user.id
              this.getMessageInfo(id)
              break;
          }
        },
        showEditView(data) {
          console.log(data)
          this.title = '编辑房屋信息';
          this.updateInfo.hid = data.hid;
          this.updateInfo.name = data.name;
          this.updateInfo.money = data.money;
          this.updateInfo.area = data.area;
          this.updateInfo.cx = data.cx;
          this.updateInfo.czfs = data.czfs;
          this.updateInfo.housemodel = data.housemodel;
          this.updateInfo.xqname = data.xqname;
          this.updateInfo.tag = [data.tag];
          this.updateInfo.dt = data.dt.toString()
          this.updateInfo.city = [data.addressCity,data.addressQx,data.addressJd]
          this.updateInfo.fwpt = [data.ptChuang==1?'床':'',data.ptXyj==1?'洗衣机':'',data.ptKt==1?'空调':'',
          data.ptBx==1?'冰箱':'',data.ptWsj==1?'卫生间':'',data.ptKzf==1?'可做饭':'']
          this.updateInfo.picture = data.picture
          const fileList = data.picture.substring(0,data.picture.length-1).split(',')
          let map;
          let fileList1=[];
          for(var i =0;i < fileList.length;i++){
            map = {url:fileList[i]}
            fileList1.push(map)
          }
          this.fileList = fileList1;
          this.dialogVisible = true;
        },
        doUpdateHouse(){
          console.log(this.updateInfo)
          this.putRequest("/house/update",this.updateInfo).then(resp => {
            if (resp.data.description=='SUCCESS') {
              this.getHouseByState();
              this.$message({
                type: 'success',
                message: '更新成功'
              });
            }
          })
        },
        del(data){
          console.log(data.hid)
          this.$confirm('此操作将永久删除【' + data.name + '】, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteRequest("/house/"+data.hid).then(resp => {
              if (resp.data.description=='SUCCESS') {
                this.getHouseByState();
                this.$message({
                  type: 'success',
                  message: '删除成功'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },




        getUserInfo(token){
          if (token != null){
            this.postRequest('/user/dologin',token).then(resp=>{
              if (resp!=null){
                this.user = resp.data;
              }
            })
          }
        }
      }
    }
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
</style>



