<template>
  <div>
    <div style="background-color: #303035;height: 50px">
    <el-row>
      <el-col :span="4">
        <div class="title" @click="toIndex">首页</div>
      </el-col>
      <el-col :span="4" style="float: right">
        <i class="el-icon-user-solid" style="font-size: 25px;color:#9399a5"></i>
        <el-link v-if="this.loginStatus==0" @click="logine()" class="title1" :underline="false">登录/注册</el-link>
        <el-dropdown v-else @command="handleCommand" style="margin-top: 15px">
              <span class="el-dropdown-link">
                {{user.pname}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">我的消息</el-dropdown-item>
            <el-dropdown-item command="b">看房记录</el-dropdown-item>
            <el-dropdown-item command="c" divided>退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
    </div>
    <br>
    <el-row >
      <el-col :span="12">
        <h1>{{houseData.name}}</h1>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12" :offset="2">
        <span style="color: red;font-size: 20px">{{houseData.money}}元/月</span>
        <el-divider direction="vertical"></el-divider>
        <span style="font-size: 18px">{{houseData.housemodel}}</span>
        <el-divider direction="vertical"></el-divider>
        <span style="font-size: 18px">{{houseData.area}}平方米</span>
        <el-divider direction="vertical"></el-divider>
        <el-tag>{{houseData.tag}}</el-tag>
        <el-tag type="danger">{{houseData.czfs}}</el-tag>
        <el-tag type="waring">{{houseData.cx}}</el-tag>
        <el-tag class="success" v-if="houseData.dt==1">有电梯</el-tag>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12" :offset="4">
          <el-carousel  indicator-position="outside" height="500px" style="margin-top: 10px">
            <el-carousel-item v-for="(item,index) in imagesbox" :key="index">
<!--            <el-card style="width:100%">-->
               <img :src="item" class="image" width="100%" height="100%">
<!--            </el-card>-->
            </el-carousel-item>
          </el-carousel>
      </el-col>
      <el-col :span="6">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>发布人信息</span>
          </div>
          <el-avatar :size="70" :src="houseOfUser.userface"></el-avatar>
          <el-row>
            <span style="float: left">姓名：{{houseOfUser.pname}}</span>
          </el-row>
          <el-row>
            <span style="float: left">联系电话：{{houseOfUser.username.substring(0,3)}}****{{houseOfUser.username.substring(7,11)}}</span>
          </el-row>
        </el-card>
        <el-button type="warning"
                   v-if="houseOfUser.id != user.id"
                   @click="order"
                   style="margin-top: 60px">立即预约吧
        </el-button>
      </el-col>
    </el-row>
    <el-card style="width: 50%;margin-left: 250px">
    <el-row :gutter="20">
      <el-col :span="24">
        <span style="font-size: 21px;float: left">房屋信息</span><span style="float: right;font-size: 12px">发布时间：{{houseData.publishTime.substring(0,10)}}</span>
        <el-divider></el-divider>
        <span style="color: red;font-size: 20px;float: left">{{houseData.money}}元/月</span>
      </el-col>
    </el-row>
    <el-row :gutter="20" style="font-size: 15px">
      <el-col :span="24">
        <span style="float: left">户型：{{houseData.housemodel}}</span>
        <span style="margin-left: 40px">面积：{{houseData.area}}平方米</span>
        <span style="margin-left: 200px">朝向：{{houseData.cx}}</span>
      </el-col>
    </el-row>
    <br>
    <el-row :gutter="20" style="font-size: 15px">
      <el-col :span="24">
        <span style="float: left">楼层：中层</span>
        <span style="margin-left: 40px">装修：精装修</span>
        <span style="margin-left: 230px">类型：{{houseData.czfs}}</span>
      </el-col>
    </el-row>
    <br>
    <el-row :gutter="20" style="font-size: 15px">
      <el-col :span="24">
        <span style="float: left">小区：{{houseData.xqname}}</span>
      </el-col>
    </el-row>
    <el-row :gutter="20" style="margin-top: 10px">
      <el-col :span="24">
        <span style="font-size: 21px;float: left">房屋配套</span>
        <el-divider></el-divider>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <img src="../../static/img/chuang.png" class="img" height="60" width="60" v-if="houseData.ptChuang==1">
        <img src="../../static/img/bingxiang.png" class="img" height="60" width="60" v-if="houseData.ptBx==1"/>
        <img src="../../static/img/xiyiji.png" class="img" height="60" width="60" v-if="houseData.ptXyj==1"/>
        <img src="../../static/img/paper.png" class="img" height="60" width="60" v-if="houseData.ptWsj==1"/>
        <img src="../../static/img/pan.png" class="img" height="60" width="60" v-if="houseData.ptKzf==1"/>
        <img src="../../static/img/kongtiao.png" class="img" height="60" width="60" v-if="houseData.ptKt==1"/>
      </el-col>
    </el-row>
    </el-card>
<!--    <el-dialog-->
<!--      title="提示"-->
<!--      :visible.sync="dialogVisible"-->
<!--      width="30%">-->
<!--      <div>-->
<!--        <el-form ref="empForm">-->
<!--          <el-row>-->
<!--              <el-form-item label="租期:" prop="name">-->
<!--                <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="orderTime"-->
<!--                          placeholder="请输入要租期"></el-input>月-->
<!--              </el-form-item>-->
<!--          </el-row>-->
<!--        </el-form>-->
<!--      </div>-->
<!--      <span slot="footer" class="dialog-footer">-->
<!--    <el-button @click="dialogVisible = false">取 消</el-button>-->
<!--    <el-button type="primary">确 定</el-button>-->
<!--  </span>-->
<!--    </el-dialog>-->
  </div>
</template>
<script type="text/javascript">
  export default{
    name: 'houseInfo',
    data () {
      return {
        loginStatus : 0,
        orderTime : '',
        dialogVisible: false,
        user : "",
        houseOfUser :'',
        imagesbox:'',
        houseData:this.$route.params.house
      }
    },
    mounted() {
      let token = localStorage.getItem("token");
      this.getUserInfo(token);
      this.getUserByHouse();
      this.imagesbox = this.houseData.picture.substring(0,this.houseData.picture.length-1).split(',')
      console.log(this.$route.params.house)
      console.log(this.imagesbox)
    },
    methods:{
      toIndex(){
        this.$router.push({path:'/'})
      },
      getUserInfo(token){
        if (token != null){
          this.postRequest('/user/dologin',token).then(resp=>{
            if (resp!=null){
              this.user = resp.data;
              this.loginStatus = 1;
            }
          })
        }
      },
      handleCommand(command) {
        switch (command) {
          case 'a' :alert('click on item ' + command)
            break;
          case 'b' :alert('b')
            break;
          case 'c' : this.logout()
            break;
        }

      },
      logout(){
        localStorage.removeItem("token");
        this.getRequest('user/logout')
        this.loginStatus=0
        this.$router.push('/')
      },
      getUserByHouse(){
        let pid = this.houseData.pid
        this.getRequest("/user/findById?pid="+pid).then(resp =>{
          this.houseOfUser = resp.data;
        })
      },
      order(){
        // this.dialogVisible = true;

        let token = localStorage.getItem("token");
        if (token == null){
          this.$router.push({path:"login",query:{path:'rentOut'}});
        }else {
          this.$confirm('预定这套房屋租期（单位月）', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            showInput:true,
            inputValue:'',
            type: 'warning'
          }).then(() => {
            let order = {
              uid:this.user.id,
              hid:this.houseData.hid
            };
            this.postRequest("/house/order",order).then(resp => {
                console.log(resp)
              if (resp.data.description == 'SUCCESS'){
                this.$message({
                  type: 'success',
                  message: '预定成功!'
                });
                this.$router.push({path:'/'})
              }else{
                this.$message({
                  type: 'success',
                  message: '预定失败，请联系管理员!'
                });
              }
            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消预定'
            });
          });
        }
      }
    }
  }
</script>
<style scoped>
  .img{
    margin-left: 20px;
  }
  .el-dropdown-link {
    cursor: pointer;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title {
    margin-top: 10px;
    font-size: 30px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title1 {
    margin-top: 10px;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    margin-left: 10px;
    width: 380px;
  }
</style>



