<template>
    <div class="sort-imgs">
        <div class="sort-img">
        <div style="float: left;">
          <img width="180" height="135" :src="house.picture.split(',')[0]" alt="">
          <!-- <%=json.main_img_path%> -->
        </div>
        <div class="sort-img-content">
          <router-link :to="{name:'houseInfo',params:{house:house}}"><h3 style="margin-top: 0px;">
            <span>{{house.name}}</span></h3>
          </router-link>
          <div class="junjia">{{house.money}}元/月<span class="junjia-word"></span></div>

          <h4 style="margin-top: -7px;"><span>{{house.housemodel}}<span ></span> | <span> {{house.area}}平米</span> | 中层</span></h4>
          <h4 style="margin-top: -7px;"><span>{{house.xqname}} </span> <span>{{house.addressQx}}</span>-<span>{{house.addressJd}}</span></h4>
          <el-tag v-if="house.tag!=''">{{house.tag}}</el-tag>
          <el-tag type="danger">{{house.czfs}}</el-tag>
          <el-tag type="waring">{{house.cx}}</el-tag>
          <el-tag class="success" v-if="house.dt==1">有电梯</el-tag>
        </div>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default{
      props:['house'],
      data (){
        return {
          // house:'',//获取attrs属性
        }
      },
      watch:{
        house(){
          this.house = this.house
        }
      },
      created() {
      }
    }
</script>
<style scoped>
  .sort-imgs{
    /*border-top: 1px solid #e6e6e6;*/
    /*border-bottom:  1px solid #e6e6e6;*/
    margin-bottom: 20px;
    margin-top: 20px;
    margin-left: 10px;
    /*border: solid;*/
    height: 170px;
    width: 85%;
  }
  .sort-img{
    position: relative;
    border-bottom:  1px dashed #e6e6e6;
    padding: 20px 0 15px;
  }
  .sort-img-content{    float: left;
    margin-left: 35px;
  }
  .sort-img-content h3{
    font-size: 20px;
    color: #000;
  }
  .sort-img-content h4{
    color: #666;
    height: 22px;
    line-height: 22px;
    font-size: 14px;
  }
  .junjia{
    position: absolute;
    text-align: right;
    right: 20px;
    color: red;
    font-size: 22px;
  }
  .junjia-word{
    color: #f60;
    font-size: 27px;
  }
  .tag{
    display: inline-block;
    color: #999;
    background: #f8f8f8;
    padding: 1px 4px;
    text-align: center;
    margin-right: 6px;
    *margin-right: 3px;
    vertical-align: top;
  }
  .sort-img-content .span-green{
    color: #799374;
    background: #f2f6ed;
  }
</style>



