<template>
  <div class="login-container">
    <div class="background">
      <img src="../../static/img/登录页背景.png" width="100%" height="100%" alt="" />
    </div>
    <el-form :model="ruleForm2" :rules="rules2"
             status-icon
             ref="ruleForm2"
             label-position="left"
             label-width="0px"
             class="demo-ruleForm login-page">
      <h3 class="title">欢迎登录</h3>
      <el-form-item prop="username">
        <el-input type="text"
                  v-model="ruleForm2.username"
                  auto-complete="off"
                  placeholder="请输入手机号..."
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password"
                  v-model="ruleForm2.password"
                  auto-complete="off"
                  placeholder="密码"
        ></el-input>
      </el-form-item>
      <el-checkbox
        v-model="checked"
        class="rememberme"
      >记住密码</el-checkbox>
      <el-link @click="toRegister()" type="primary" :underline="false" style="margin-left: 130px">还没有帐号？赶紧注册吧~</el-link>

      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      //手机号验证
      var validateMobilePhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('负责人手机号不可为空'));
        } else {
          if (value !== '') {
                var reg=/^1[3456789]\d{9}$/;
                if(!reg.test(value)){
                  callback(new Error('请输入有效的手机号码'));
                }
            }
          callback();
        }
        };
      //密码验证
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不可为空'));
        } else {
          if (value !== '') {
            var reg=/^1[3456789]\d{9}$/;
            if(!reg.test(value)){
              callback(new Error('请输入有效的密码'));
            }
          }
          callback();
        }
      };
      return {
        logining: false,
        ruleForm2: {
          username: '',
          password: '',
        },
        rules2: {
          username: [{ validator: validateMobilePhone, trigger: "blur" }],
          password: [{required: true, message: '密码不能为空', trigger: 'blur'},
            { min: 3, max: 6, message: "长度在 3 到 6 个字符", trigger: "blur" }],
        },
        checked: false
      }
    },
    methods: {
      toRegister(){
        this.$router.push({path:'register'})
      },
      handleSubmit(event){
        this.$refs.ruleForm2.validate((valid) => {
          if(valid){
            this.logining = true;
            this.postRequest('/user/login',this.ruleForm2).then(resp => {
              if (resp.data.description == "SUCCESS"){
                localStorage.setItem("token",resp.data.detail)
                // localStorage.setItem("user",JSON.stringify(resp.data.detail))
                this.logining = false;
                let path = this.$route.query.path;
                if (path==''||path==undefined){
                  this.$router.push({path:'/'})
                }else{
                  this.$router.push({path:'/'+path})
                }
              }
            })
          }else{
            return false;
          }
        })
      }
    }
  };
</script>

<style scoped>
  .login-container {
    width: 100%;
    height: 100%;
    z-index:1;
    position: absolute;
  }
  .login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    margin: 180px auto;
    width: 400px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .background{
    width:100%;
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: absolute;
  }
  label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;
  }
</style>



