<template>
  <div>
    <div style="background-color: #303035;height: 50px;">
    <el-row>
      <el-col :span="4">
        <div class="title" @click="toIndex">首页</div>
      </el-col>
      <el-col :span="4" style="float: right">
        <i class="el-icon-user-solid" style="font-size: 25px;color:#9399a5"></i>
        <el-link v-if="this.loginStatus==0" @click="logine()" class="title1" :underline="false">登录/注册</el-link>
        <el-dropdown v-else @command="handleCommand" style="margin-top: 15px">
              <span class="el-dropdown-link">
                {{user.pname}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">个人中心</el-dropdown-item>
            <el-dropdown-item command="b">
              <el-badge :value="mescount" style="margin-top: 10px;margin-right: 40px;">
                消息
              </el-badge>
            </el-dropdown-item>
            <el-dropdown-item command="c" divided>退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
    </div>
<!--    <hr>-->
    <el-row style="background-color: #eeeeee;">
      <el-col :span="11">
        <img src="../assets/房子1.png" height="90" width="90">
        <span style="font-size: 40px;color: #4a9e4f;font-family: 华文琥珀 ">E租房</span>
        <el-dropdown @command="handleCommandCity">
              <span class="el-dropdown-link">
                {{defaultCity}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">淄博</el-dropdown-item>
            <el-dropdown-item command="b" divided>青岛</el-dropdown-item>
            <el-dropdown-item command="c" divided>济南</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col :span="10" style="margin-top: 40px">
<!--        <el-input-->
<!--          style="width:500px" placeholder="请输入小区名称、地址..."-->
<!--          v-model="input" clearable>-->
<!--        </el-input>-->
        <el-autocomplete
          class="inline-input"
          style="width:480px"
          v-model="input"
          :fetch-suggestions="querySearch"
          placeholder="请输入小区名称..."
          :trigger-on-focus="false"
        ></el-autocomplete>
        <el-button type="success" @click="getHouseInfo()">搜索</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="18" :offset="3">
        <el-menu :default-active="activeIndex"
                 mode="horizontal"
                 background-color="#303035"
                 text-color="#eee"
                 active-text-color="#ffd04b">
          <el-menu-item index="1">区域找房</el-menu-item>
          <el-menu-item index="2">地图找房</el-menu-item>
          <el-menu-item index="3" @click="rentOut()">我要出租
            <i class="el-icon-top"></i>
          </el-menu-item>
        </el-menu>
        <div  style="border: 1px solid #a1a1a1;border-radius:4px;height: 240px;">
          <el-row>
            <span style="float: left;color: #a1a1a1">位置：</span>
            <el-link style="margin-left: 20px;float: left"
                     :underline="false"
                     :class="{'selectedColor':flag==''}">全部</el-link>
            <el-link style="margin-left: 20px;float: left"
                     @click="check(item)" :underline="false"
                     :class="{'selectedColor':flag==item}"
                     v-for="(item,index) in positionList" :key="index">{{item}}</el-link>
          </el-row><br>
          <el-row>
            <span style="float: left;color: #a1a1a1">租金：</span>
            <el-link style="margin-left: 20px;float: left"
                     :underline="false"
                     :class="{'selectedColor':flag2==''}">全部</el-link>
            <el-link style="margin-left: 20px;float: left"
                     @click="check2(item)" :underline="false"
                     :class="{'selectedColor':flag2==item}"
                     v-for="(item,index) in rentMoneyList" :key="index">{{item}}</el-link>
          </el-row><br>
          <el-row>
            <span style="float: left;color: #a1a1a1">房型：</span>
            <el-link style="margin-left: 20px;float: left"
                     :underline="false"
                     :class="{'selectedColor':flag3==''}">全部</el-link>
            <el-link style="margin-left: 20px;float: left"
                     @click="check3(item)" :underline="false"
                     :class="{'selectedColor':flag3==item}"
                     v-for="(item,index) in fxList" :key="index">{{item}}</el-link>
          </el-row><br>
          <el-row>
            <span style="float: left;color: #a1a1a1">类型：</span>
            <el-link style="margin-left: 20px;float: left"
                     :underline="false"
                     :class="{'selectedColor':flag4==''}">全部</el-link>
            <el-link style="margin-left: 20px;float: left"
                     @click="check4(item)" :underline="false"
                     :class="{'selectedColor':flag4==item}"
                     v-for="(item,index) in lxList" :key="index">{{item}}</el-link>
          </el-row><br>
          <el-row>
            <span style="float: left;color: #a1a1a1">朝向：</span>
            <el-link style="margin-left: 20px;float: left"
                     :underline="false"
                     :class="{'selectedColor':flag5==''}">全部</el-link>
            <el-link style="margin-left: 20px;float: left"
                     @click="check5(item)" :underline="false"
                     :class="{'selectedColor':flag5==item}"
                     v-for="(item,index) in cxList" :key="index">{{item}}</el-link>
          </el-row><br>
          <el-row>
            <span style="float: left;color: #a1a1a1">更多筛选：</span>
            <el-checkbox style="float: left" @change="check6" v-model="flag6">防疫服务</el-checkbox>
            <el-checkbox style="float: left" @change="check7" v-model="flag7">电梯房</el-checkbox>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <br>
    <el-row>
      <el-col :span="10">
        <span>已为您找到 {{total}} 套 {{defaultCity}}租房</span>
      </el-col>
    </el-row>
    <el-row>
      <el-menu style="margin-left: 200px" :default-active="'1'"
               class="el-menu-demo" mode="horizontal"
               @select="handleSelect">
        <el-menu-item index="1">综合排序</el-menu-item>
        <el-menu-item index="2">最新上架</el-menu-item>
        <el-submenu index="3">
          <template slot="title">价格</template>
          <el-menu-item index="3-1">价格由低到高</el-menu-item>
          <el-menu-item index="3-2">价格由高到低</el-menu-item>
        </el-submenu>
        <el-submenu index="4">
          <template slot="title">面积</template>
          <el-menu-item index="4-1">面积由小到大</el-menu-item>
          <el-menu-item index="4-2">面积由大到小</el-menu-item>
        </el-submenu>
      </el-menu>
    </el-row>
    <div class="content1" >
      <cmpContent v-if="houseData != null" v-loading="loading" v-for="(item,index) in houseData"  :key="index" :house="item"/>
      <span style="margin-top: 20px" v-if="total == 0">沒有找到匹配的房屋！</span>
    </div>
    <div class="block">
      <el-pagination
        background
        @current-change="currentChange"
        @size-change="sizeChange"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import cmpContent from '../components/cmp-content'
  import rentOut from "./rentOut";
  export default {
    name: "Index",
    components:{cmpContent},
    data(){
      return{
        restaurants: [{ "value": "金色家园", "address": "民主街" },
          { "value": "芦湖小区", "address": "芦湖路" }],
        loginStatus : 0,
        total: 0,
        page: 1,
        size: 10,
        mescount:0,
        input :"",
        user : "",
        loading:false,
        flag:"",
        flag2:"",
        flag3:"",
        flag4:"",
        flag5:"",
        flag6:false,
        flag7:false,
        pxList:null,
        defaultCity:'淄博',
        housestate:0,
        activeIndex: '1',
        active : "",
        selectvalue:'',
        screenList:'',
        options: [{
          value: '南北',
          label: '南北'
        }, {
          value: '东西',
          label: '东西'
        }],
        positionList : ['张店','高青'],
        rentMoneyList:['500元以下','500-800元','800-1000元','1000元以上'],
        fxList:['一室','两室','三室','三室以上'],
        lxList:['整租','合租'],
        cxList:['南北','东西'],
        houseData:[]
      }
    },
    created() {
      // this.user = sessionStorage.getItem("user");
    },
    mounted() {
      let token = localStorage.getItem("token");
      this.getUserInfo(token);
      this.getHouseInfo();
      this.getMessageCount();
    },
    methods:{
      toIndex(){
        this.$router.push({path:'/'})
      },
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      //提示搜索框
      handleSelectForInput(item) {
        console.log(item);
      },
      getUserInfo(token){
        if (token != null){
          this.postRequest('/user/dologin',token).then(resp=>{
            if (resp!=null){
              this.user = resp.data;
              this.loginStatus = 1;
            }
          })
        }
      },
      getMessageCount(){
        const id = this.user.id;
        if (id == null){
          let token = localStorage.getItem("token");
          this.getUserInfo(token);
        }
        this.getRequest("/user/messcount").then(resp => {
          this.mescount = resp.data.detail
        })
      },
      updateMess(){
        this.postRequest("/user/updatemessage")
      },
      handleCommand(command) {
        switch (command) {
          case 'a' :
            this.$router.push({path:'PersonalCenter'})
            break;
          case 'b' :
            this.updateMess();
            this.$router.push({name:'PersonalCenter',params:{tableSelect:'3' ,id:this.user.id}})
            break;
          case 'c' :
            this.logout()
            break;
        }

      },
      handleCommandCity(command) {
        switch (command) {
          case 'a' :this.defaultCity = '淄博'
            this.flag = ''
            this.positionList = ['张店','高青']
            this.getHouseInfo()
            break;
          case 'b' :this.defaultCity = '青岛'
            this.flag = ''
            this.positionList = ['黄岛区','市南区']
            this.getHouseInfo()
            break;
          case 'c' :alert("待开发")
            break;
        }
      },
      handleSelect(key, keyPath) {
        switch (key) {
          case '1':this.pxList = null
            this.getHouseInfo()
                break;
          case '2':this.pxList = ['publish_time','desc']
            this.getHouseInfo()
            break;
          case '3-1':this.pxList = ['money','asc']
            this.getHouseInfo()
            break;
          case '3-2':this.pxList = ['money','desc']
            this.getHouseInfo()
            break;
          case '4-1':this.pxList = ['area','asc']
            this.getHouseInfo()
            break;
          case '4-2':this.pxList = ['area','desc']
            this.getHouseInfo()
            break;
        }
      },
      logout(){
        localStorage.removeItem("token");
        this.getRequest('user/logout')
        this.loginStatus=0
        this.$router.push('/')
      },
      rentOut(){
        let token = localStorage.getItem("token");
        if (token == null){
          this.$router.push({path:"login",query:{path:'rentOut'}});
        }else {
          this.$router.push("/rentOut");
        }
      },
      sizeChange(currentSize) {
        this.size = currentSize;
        this.getHouseInfo();
      },
      currentChange(currentPage) {
        this.page = currentPage;
        this.loading = true
        this.getHouseInfo();
      },
      getHouseInfo(){//初始化数据 包括搜索条件
        this.loading = true;
        let url = '/index/find/?page=' + this.page + '&size=' + this.size + '&state=' + '0';
        url += "&addressCity="+this.defaultCity
        if (this.input != ''){
          url += "&xqname=" + this.input;
        }
        if (this.pxList != null){
          url += "&pxList=" + this.pxList;
        }
        if (this.flag != ''){
          url += "&addressQx=" + this.flag;
        }
        if (this.flag2 != ''){
          url += "&moneyqj=" + this.flag21;
        }
        if (this.flag3 != ''){
          url += "&housemodel=" + this.flag3;
        }
        if (this.flag4 != ''){
          url += "&czfs=" + this.flag4;
        }
        if (this.flag5 != ''){
          url += "&cx=" + this.flag5;
        }
        if (this.flag6 == true){
          url += "&tag=" + '防疫服务';
        }else{
          url += '';
        }
        if (this.flag7 == true){
          url += "&dt=" + 1;
        }
        this.getRequest(url).then(resp => {
            this.loading = false;
            this.houseData = resp.data.data;
            this.total = resp.data.total;
        })
      },
      logine(){
        this.$router.push("login");
      },
      defult(){
        this.flag = '';
      },
      check(e){
        this.flag = e;
        this.screenList = this.flag
        this.getHouseInfo();
      },
      check2(e){
        console.log('选中的是'+e)
        this.flag2 = e;
        if (e == '500元以下'){
          this.flag21 = [0,500];
        }
        if (e == '500-800元'){
          this.flag21 = [500,800];
        }
        if (e == '800-1000元'){
          this.flag21 = [800,1000];
        }
        if (e == '1000元以上'){
          this.flag21 = [1000,99999];
        }
        this.getHouseInfo();
      },
      check3(e){
        console.log('选中的是'+e)
        this.flag3 = e;
        this.getHouseInfo();
      },
      check4(e){
        console.log('选中的是'+e)
        this.flag4 = e;
        this.getHouseInfo();
      },
      check5(e){
        console.log('选中的是'+e)
        this.flag5 = e;
        this.getHouseInfo();
      },
      check6(){
        this.getHouseInfo();
      },
      check7(){
        this.getHouseInfo();
      }
    }
  }
</script>

<style scoped>
  .content1{
    margin-top: 20px;
    margin-left: 180px;
    cursor:pointer;
    /*&.active{*/
    /*  background: #0190fe;*/
    /*  color: #fff;  // 选中字体背景跟着改变*/

    /*  }*/
  }

  .activeclass{
    background-color: red;
  }

  .selectedColor {
    color: #2d8cf0;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }

  .el-dropdown-link {
    cursor: pointer;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title {
    margin-top: 10px;
    font-size: 30px;
    font-family: 华文行楷;
    color: #9399a5
  }
  .title1 {
    margin-top: 10px;
    font-size: 20px;
    font-family: 华文行楷;
    color: #9399a5
  }
</style>



